@font-face {
    font-family: "font-1";
    src: url(../assets/fonts/Poppins-Medium.ttf);
}

$font-1: 'Arvo', sans-serif;

$color-1 : #e2c000;
$color-2 : #02199C;
$color-3 : #199C02;
$color-4 : #d40020;
$white: #d1d1d1;

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style-type: none;
    outline: none;
    border: none;
    text-decoration: none;
}

body {
    font-family: $font-1;
    height: 100vh;
    background: url(../assets/img/background.jpg) no-repeat 50% 50% fixed;
    background-size: 160%;
    color: $white ;
    animation: bgScale 12s infinite ease-in-out;
}

@keyframes bgScale {
    0% {
        background-size: 160%;

    }
    50% {
        background-size: 250%;
     
    }
    100% {
        background-size: 160%;

 
    }
}


