.card {
    border-radius: 5px;
    background: $color-1;
    margin: 15px 20px 0 0;
    padding: 10px;
    width: 380px;
    height: 600px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    transition: all 0.3s ease-in-out;
    color: $color-4;
}
.card img {
    width: fit-content;
    height: 300px;
    border-radius: 5px;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.5);
}

.card h3 {
    font-size: 1.5rem;
    padding: 0;
    height: 60px;
}

.par {
    font-size: 1rem;
    padding: 0;
    color: $white;
}

.vote {
    margin: 0 7% 0 0; 
    color: $white;
}

.card span {
    font-size: 1.2rem;
    padding: 0 0 0 5%;
    color: $color-4;
}

.vote span {
    font-size: 1.2rem;
    padding: 0 0 0 15%;
}

.describe { 
    font-size: 1rem;
    padding: 2% 0 0 0;
    color: $color-4;
    height: 99px;
    margin-bottom: 30px;
    overflow: hidden;
}

.genre {
    font-size: 1rem;
    padding: 0;
    color: $color-4;
    height: 55px;
    margin-bottom: 10px;
    overflow: hidden;
}

.card:hover {
    transform: scale(1.05);
    background: $color-2;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.5);
}

.card:hover h3 {
    color: $color-3;
}

.card:hover .par span {
    color: $color-3;
}

.card:hover .vote span {
    color: $color-3;
}

.card:hover .describe {
    color: $color-3;
}

.card:hover button {
    background: $color-3;
    color: $white;
    border: 1px solid $color-2;
}

button {
    border-radius: 5px;
    background: $color-2;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    color: $white;
    font-size: 1rem;
    box-shadow: 0 0 10px 0 $color-2;
    padding: 5px;
    margin-bottom: px;
}

