.search-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    
}

.search-container form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.search-container form input {
    width: 45%;
    height: 50px;
    background-color: transparent;
    text-align: center;
    color: $white;
    border-radius: 10px;
    border: 2px solid $color-1;
    padding: 0 1rem;
    font-size: 1.2rem;
    letter-spacing: 1px;
    margin: 0.5rem 0;
}

.sort {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin:0 0 2rem 0 ;
}

.sort label {
    color: $white;
    font-size: 1.2rem;
    letter-spacing: 1px;
    margin: 0 1rem;
}

.card-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin: 0 1rem;
}
