.navigation {
    height: 50px;
    width: 100%;
    margin:0 0 2% 0;
}

.navigation-list {
    display: flex;
    height: 100%;
    justify-content:center;
    align-items: center;
}

.navigation-list a {
    color: $color-4;
    margin: 0 3% 0 3%;
    font-size: 1.2rem;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 1px;
    opacity: 1 ;
}

.navigation-list a:hover {
    color: $color-1;
    transform: scale(1.1);
    transition: all 0.3s;
}

.navigation-list a.active {
    color: $color-1;
    transform: scale(1.4);
    transition: all 0.3s;
}